<template>
    <section>
        <div class="image-area">
            <img  src="../assets/images/b_black.jpg" alt="b_black" class="productsImg show">
            <img  src="../assets/images/b_white.jpg" alt="b_white" class="productsImg">
        </div>

        <div class="product">
            <div class="product-info">
                <h3>잭퍼셀 클래식 블랙</h3>
                <p>79,000원</p>
                <p>남녀공용</p>
                <p>32건 | <span class="star">★</span> 4.9</p>
                <p>잭 퍼셀의 미소를 상징하는 아이코닉 '스마일 토' 디테일</p>
            </div>

            <div class="color">
                <p>컬러 <span class="bold">블랙</span></p>
                <img  src="../assets/images/s_black.jpg" alt="s_black" @click="changeBlack" class="black">
                <img  src="../assets/images/s_white.jpg" alt="s_white" @click="changeWhite" class="white">
            </div>

            <div class="size">
                <button class="size-guide-btn" @click="$emit('openSize')">사이즈가 고민되면 클릭하세요!</button>
                <p class="size-guide">사이즈 가이드</p>

                <div class="sizes-area">
                    <div class="sizes" v-for="(a, i) in sizes" :key="i" @click="currentTab=i" :class="{'active':currentTab==i}">{{sizes[i]}}</div>
                </div>
            </div>

            <div class="count">
                <p class="amount">{{count}}</p>
                <button class="plus" @click="plusCount">+</button>
                <button class="minus" @click="minusCount">-</button>
                <h3 class="price">{{price.toLocaleString()}}원</h3>
            </div>

            <div class="order">
                <button class="cart-btn">장바구니</button>
                <button class="order-btn">구매하기</button>
                <img src="../assets/images/emptyHeart.png" alt="empty_heart" @mouseenter="changePic" @mouseout="originalPic"  class="heart">
            </div>

            <div class="sns">
                <a href="https://www.facebook.com/converse" target="_blank"><img src="../assets/images/sns_logos/Facebook.png" alt="Facebook"></a>
                <a href="https://www.instagram.com/converse/" target="_blank"><img src="../assets/images/sns_logos/Instagram.png" alt="Instagram"></a>
                <a href="https://twitter.com/Converse" target="_blank"><img src="../assets/images/sns_logos/Twitter.png" alt="Twitter"></a>
            </div>
        </div>
    </section>
</template>

<script>
import Modal from './Modal.vue'

export default {
    name: "converse",
    data() {
        return {
            count: 0,
            price: 0,
            sizes: [
                "220", "225", "230", "235", "240", "245", "250", "255","260", "265","270", "275", "280","285", "290", "295", "300"
            ],
            currentTab:0,
        }
    },
    component: {
        Modal
    },
    props: {
        // isModal: Boolean
    },
    methods: {
        plusCount(){
            if(this.count >= 10){
                    alert("최대 주문 수량은 10개입니다.");
                    this.count = 0;
                    this.price = 0;
                } else {
                    this.count++;
                    this.price = this.price + 79000;
            }
        },
        minusCount(){
            if(this.count <= 0){
                alert("최소 주문 수량은 1개입니다.");
                this.count = 0;
                this.price = 0;
            } else{
                this.count--;
                this.price = this.price - 79000;
            }
        },
        changeBlack(){
            let product = document.querySelectorAll("div.image-area img");

            let i;
            for(i = 0; i < product.length; i++)
            document.querySelectorAll("div.image-area img")[i].classList.remove('show');
            product[0].classList.add("show");
        },
        changeWhite(){
            let product = document.querySelectorAll(".productsImg");

            let i;
            for(i = 0; i < product.length; i++)
            document.querySelectorAll("div.image-area img")[i].classList.remove('show');
            product[1].classList.add("show");
        },
        changePic(){
            let heart = document.querySelector(".heart");
            heart.classList.add("fillheart")
            // heart.src = "../asstes/images/fillHeart.png"
        },
        originalPic(){
            let heart = document.querySelector(".heart");
            heart.classList.remove("fillheart")
        },
    },
}
</script>

<style scoped>
