<template>
  <section>
    <!-- header -->
    <div class="hheader">
      <ul class="header">
        <li>
          <h1 class="logo">
            <a href="/"><img src="../assets/logo.png" alt="logo"></a>
          </h1>
        </li>
        <li class="entireBox">
          <!-- select박스 -->
          <div>
            <select class="searchBox1" name="menu" id="menu">
              <option value="전체">전체</option>
              <option value="과자">과자</option>
              <option value="음료수">음료수</option>
              <option value="빵">빵</option>
              <option value="신발">신발</option>
              <option value="옷">옷</option>
            </select>
          </div>
          <!-- search박스 -->
          <div>
            <input class="searchBox2" type="search" placeholder="찾고 싶은 상품을 검색해보세요!" v-model="searchText" />
          </div>
          <!-- 마이쿠팡 -->
          <div>
                  <div class="myCoupang">
                    <img src="../assets/user.png" alt="user">
                    <p>마이쿠팡</p>
                <div class="myCoupang--text">
                  <p>주문목록</p>
                  <p>취소/반품</p>
                  <p>찜 리스트</p>
                </div>
              </div>
            </div>
            <!-- 장바구니 -->
          <div>
            <div class="myCoupang">
                    <img src="../assets/cart.png" alt="user">
                    <p>장바구니</p>
                <div class="myCoupang--text cartText">
                  <p>장바구니에 담긴 상품이 없습니다.</p>
                </div>
            </div>
          </div>
          <!-- search박스 아이콘 -->
          <div class="search--item">
            <img src="../assets/mike.png" alt="mike">
            <img src="../assets/search.png" alt="search">
          </div>
        </li>
        <!-- 우상단 login박스 -->
        <li class="loginBox">
          <button>Login</button>
          <button>Join</button>
        </li>
      </ul>
    </div>
    <!-- header끝 -->
    <!-- 오늘의 발견 박스 -->
    <div class="todayBigBigBox">
      <h2>오늘의 발견 <span class="todayBox--text">오늘 쿠팡이 엄선한 가장 hot한 상품!</span></h2>
      <div class="todayBigBox">
        <div class="todayBox">
          <div class="todayBox--content" @click="levelup()">
            <img src="../assets/새우깡모음.png" alt="새우깡">
          </div>
          <div class="todayBox--content" @click="levelup2()">
            <img src="../assets/mainconverse.png" alt="컨버스" :style="{'paddingTop':'30px'}">
            <img src="../assets/mainconverse2.png" alt="컨버스">
            
          </div>
        </div>
      </div>
    </div>
    <!-- 정렬 -->
    <div class="sortBar">
      <span class="sort" @click="rankSort(), currentTab1=1" :class="{'active':currentTab1===1}">별점순 </span>
        <span class="sort" @click="cheapSort(), currentTab1=2" :class="{'active':currentTab1===2}" > 낮은가격순 </span>
        <span class="sort" @click="expSort(), currentTab1=3" :class="{'active':currentTab1===3}"> 높은가격순 </span>
        <span class="sort" @click="saleSort(), currentTab1=4" :class="{'active':currentTab1===4}"> 판매량순 </span>
        <span class="sort" @click="newSort(), currentTab1=5" :class="{'active':currentTab1===5}"> 빠른도착순</span>
    </div>

      <!-- 메뉴바 -->
      <div class="menuBar">
        <div class="menuBarBar">
          <a href="##"><img src="../assets/category.png" alt="category"></a>
          <div class="menuBar--content">
            <li @click="step=0, ccurrentTab2=1" :class="{'active2':ccurrentTab2===1}">과자</li>
            <li @click="step=1, ccurrentTab2=2" :class="{'active2':ccurrentTab2===2}">음료수</li>
            <li @click="step=2, ccurrentTab2=3" :class="{'active2':ccurrentTab2===3}">빵</li>
            <li @click="step=3, ccurrentTab2=4" :class="{'active2':ccurrentTab2===4}">신발</li>
            <li @click="step=4, ccurrentTab2=5" :class="{'active2':ccurrentTab2===5}">옷</li>
          </div>
        </div>
      </div>
    <!-- 과자필터 -->
    <div class="item-box" v-if="step==0">
      <div v-for="(a,i) in snackFilter" :key="i" class="item">
        <h4>{{ a.title }}</h4>
      <p class="profile ttext" :style="{backgroundImage:`url(${a.image})`}"></p>
      <p class="price text ttext">{{ a.price.toLocaleString() }}원</p>
      <p class="bonus ttext"><img src="../assets/dollar.png" alt="money"> 최대 {{ (a.price / 20) }}원 적립</p>
      <p class="ship text ttext">{{ 요일(a.date.getDay()) }} {{a.date.getMonth()}}/{{a.date.getDate()}} 도착 예정</p>
      <p class="star" :style="{width:(20*a.grade)+'px'}">({{ a.grade }})</p>
      </div>
      <ul>
        <li><img src="../assets/review.png" alt="리뷰"></li>
        <li class="review">{{ like1 }}명에게 도움 됨 <span @click="like1++">도움이 돼요</span> <span @click="like1--">도움이 안 돼요</span></li>
      </ul>
    </div>

    <!-- 음료수필터 -->
    <div class="item-box" v-if="step==1">
      <div v-for="(a,i) in beverageFilter" :key="i" class="item">
        <h4>{{ a.title }}</h4>
      <p class="profile ttext" :style="{backgroundImage:`url(${a.image})`}"></p>
      <p class="price text ttext">{{ a.price.toLocaleString() }}원</p>
      <p class="bonus ttext"><img src="../assets/dollar.png" alt="money"> 최대 {{ (a.price / 20) }}원 적립</p>
      <p class="ship text ttext">{{ 요일(a.date.getDay()) }} {{a.date.getMonth()}}/{{a.date.getDate()}} 도착 예정</p>
      <p class="star" :style="{width:(20*a.grade)+'px'}">({{ a.grade }})</p>
      </div>
      <ul>
        <li><img src="../assets/review2.png" alt="리뷰"></li>
        <li class="review">{{ like2 }}명에게 도움 됨 <span @click="like2++">도움이 돼요</span> <span @click="like2--">도움이 안 돼요</span></li>
      </ul>
    </div>

    <!-- 빵필터 -->
    <div class="item-box" v-if="step==2">
      <div v-for="(a,i) in breadFilter" :key="i" class="item">
        <h4>{{ a.title }}</h4>
      <p class="profile ttext" :style="{backgroundImage:`url(${a.image})`}"></p>
      <p class="price text ttext">{{ a.price.toLocaleString() }}원</p>
      <p class="bonus ttext"><img src="../assets/dollar.png" alt="money"> 최대 {{ (a.price / 20) }}원 적립</p>
      <p class="ship text ttext">{{ 요일(a.date.getDay()) }} {{a.date.getMonth()}}/{{a.date.getDate()}} 도착 예정</p>
      <p class="star" :style="{width:(20*a.grade)+'px'}">({{ a.grade }})</p>
      </div>
      <ul>
        <li><img src="../assets/review3.png" alt="리뷰"></li>
        <li class="review">{{ like3 }}명에게 도움 됨 <span @click="like3++">도움이 돼요</span> <span @click="like3--">도움이 안 돼요</span></li>
      </ul>
    </div>

    <!-- 신발필터 -->
    <div class="item-box" v-if="step==3">
      <div v-for="(a,i) in shoesFilter" :key="i" class="item">
        <h4>{{ a.title }}</h4>
      <p class="profile ttext" :style="{backgroundImage:`url(${a.image})`}"></p>
      <p class="price text ttext">{{ a.price.toLocaleString() }}원</p>
      <p class="bonus ttext"><img src="../assets/dollar.png" alt="money"> 최대 {{ (a.price / 20) }}원 적립</p>
      <p class="ship text ttext">{{ 요일(a.date.getDay()) }} {{a.date.getMonth()}}/{{a.date.getDate()}} 도착 예정</p>
      <p class="star" :style="{width:(20*a.grade)+'px'}">({{ a.grade }})</p>
      </div>
      <ul>
        <li><img src="../assets/review4.png" alt="리뷰"></li>
        <li class="review">{{ like3 }}명에게 도움 됨 <span @click="like3++">도움이 돼요</span> <span @click="like3--">도움이 안 돼요</span></li>
      </ul>
    </div>

    <!-- 옷필터 -->
    <div class="item-box" v-if="step==4">
      <div v-for="(a,i) in clothesFilter" :key="i" class="item">
        <h4>{{ a.title }}</h4>
      <p class="profile ttext" :style="{backgroundImage:`url(${a.image})`}"></p>
      <p class="price text ttext">{{ a.price.toLocaleString() }}원</p>
      <p class="bonus ttext"><img src="../assets/dollar.png" alt="money"> 최대 {{ (a.price / 20) }}원 적립</p>
      <p class="ship text ttext">{{ 요일(a.date.getDay()) }} {{a.date.getMonth()}}/{{a.date.getDate()}} 도착 예정</p>
      <p class="star" :style="{width:(20*a.grade)+'px'}">({{ a.grade }})</p>
      </div>
      <ul>
        <li><img src="../assets/review5.png" alt="리뷰"></li>
        <li class="review">{{ like3 }}명에게 도움 됨 <span @click="like3++">도움이 돼요</span> <span @click="like3--">도움이 안 돼요</span></li>
      </ul>
    </div>
  </section>
</template>

<script>
import shopData from '../assets/snack.js'
import shopData1 from '../assets/beverage.js'
import shopData2 from '../assets/bread.js'
import shopData3 from '../assets/shoes.js'
import shopData4 from '../assets/clothes.js'
export default {
  name:'container',
  computed: {
    snackFilter() {
      return this.과자.filter((item) =>
        item.title.includes(this.searchText)
      )
    },
    beverageFilter() {
      return this.음료수.filter((item) =>
        item.title.includes(this.searchText)
      )
    },
    breadFilter() {
      return this.빵.filter((item) =>
        item.title.includes(this.searchText)
      )
    },
    shoesFilter() {
      return this.신발.filter((item) =>
        item.title.includes(this.searchText)
      )
    },
    clothesFilter() {
      return this.옷.filter((item) =>
        item.title.includes(this.searchText)
      )
    }
  },
    data(){
        return{
            like1:15,
            like2:37,
            like3:22,
            searchText: '',
            step:0,
            과자:shopData,
            음료수:shopData1,
            빵:shopData2,
            신발:shopData3,
            옷:shopData4,
            currentTab1:0,
            ccurrentTab2:1,
        }
    },
  methods: {
    levelup(){
      this.$emit('levelup');
    },
    levelup2(){
      this.$emit('levelup2');
    },
      요일(a){
        const week= ['금(Fri)', '토(Sat)', '일(Sun)', '월(Mon)', '화(Tue)', '수(Wed)', '목(Thu)']
        return week[a];},
    rankSort(){ //별점순
      this.과자.sort(function(a,b){
        return b.grade-a.grade
      });
      this.음료수.sort(function(a,b){
        return b.grade-a.grade
      });
      this.빵.sort(function(a,b){
        return b.grade-a.grade
      });
      this.신발.sort(function(a,b){
        return b.grade-a.grade
      });
      this.옷.sort(function(a,b){
        return b.grade-a.grade
      })
    },
    cheapSort(){
      this.과자.sort(function(a,b){
        return a.price-b.price
      });
      this.음료수.sort(function(a,b){
        return a.price-b.price
      });
      this.빵.sort(function(a,b){
        return a.price-b.price
      });
      this.신발.sort(function(a,b){
        return a.price-b.price
      });
      this.옷.sort(function(a,b){
        return a.price-b.price
      })
    },
    expSort(){
      this.과자.sort(function(a,b){
        return b.price-a.price
      });
      this.음료수.sort(function(a,b){
        return b.price-a.price
      });
      this.빵.sort(function(a,b){
        return b.price-a.price
      });
      this.신발.sort(function(a,b){
        return b.price-a.price
      });
      this.옷.sort(function(a,b){
        return b.price-a.price
      })
    },
    saleSort(){
      this.과자.sort(function(a,b){
        return b.sold-a.sold
      });
      this.음료수.sort(function(a,b){
        return b.sold-a.sold
      });
      this.빵.sort(function(a,b){
        return b.sold-a.sold
      });
      this.신발.sort(function(a,b){
        return b.sold-a.sold
      });
      this.옷.sort(function(a,b){
        return b.sold-a.sold
      })
    },
    newSort(){
      this.과자.sort(function(a,b){
        return a.date-b.date
      });
      this.음료수.sort(function(a,b){
        return a.date-b.date
      });
      this.빵.sort(function(a,b){
        return a.date-b.date
      });
      this.신발.sort(function(a,b){
        return a.date-b.date
      });
      this.옷.sort(function(a,b){
        return a.date-b.date
      })
      },
    },
    props:{
       
    }
}
</script>

