<template>
    <section>
          <!-- 모달창 얍얍얍ㅇ -->
      <div class="priceWraps" style="position:relative">
        <transition name="shrimpBox">
      <div class="cartBg" v-if="cartPage == true">
        <div class="cartBox" >장바구니에 담겼습니다.
        <div class="closeBtn" @click="cartPage = false"> 확인 </div>
        </div>
      </div>
      </transition>
      <transition name="shrimpBox">
      <div class="cartBg" v-if="memberships == true">
        <div class="cartBox memberships" > 멤버십 가입페이지로 <br> 가시겠습니까?
        <div class="btnWraps">
          <div class="closeBtn closeBtn2" @click="memberships = false"> 확인 </div>
          <div class="closeBtn closeBtn2" @click="memberships = false"> 취소 </div>
        </div>
        </div>
      </div>
      </transition>
        <div class="bigImgbox">
          <img :src="상품[click].image" class="bigImg" alt="상세이미지" />
        </div>
        <div class="priceBox">
          <div class="titleWraps">
            <h1 class="objTitle">{{상품[click].title}}</h1>
            <p class="comment">
              <span class="objStars" :style="{width:(20 * 상품[click].grades) + 'px'}">별</span>
              {{상품[click].comment.toLocaleString()}} 개의 상품평
            </p>
            <div class="favBoxOBJ">
              <div class="favBoxOBJ1 favsOBJ" @click="favBox()"></div>
              <div class="favBoxOBJ2 favsOBJ"></div>
            </div>
          </div>
          <div class="deliver">
          <h3 class="totalPriceOBJ">{{ (number * 상품[click].price).toLocaleString() }} 원</h3>
          무료배송</div>
          <p class="small-banner"><img src="../assets/coin.png" alt="적립"> 최대 {{( number * 상품[click].price / 10).toLocaleString() }}원 적립</p>
          <div class="membership">
                멤버십 머니 결제 시 1% 적립 <br>
                [ 멤버십 + SSG페이 계좌이체 ] 결제 시 2% 적립<br>
                [ 멤버십 + SSG페이 머니 ] 결제 시 4% 추가적립<br>
                <a href="#" class="btn-membership" @click="memberships=true">멤버십 신청하기</a>
              </div>
          <div class="sellBox">
            <div class="numberBox">
              <div class="nBox num-btn minus-num" @click="minus()">-</div>
              <div class="nBox number">{{ number }}</div>
              <div class="nBox num-btn plus-num" @click="plus()">+</div>
            </div>
            <button class="btns cart-btn" @click="cartPage = true">장바구니 담기</button>
            <button class="btns buy-btn">바로 구매 > </button>
            
          </div>    
        </div>
      </div>
      
     </section>
  </template>
  
  <script>
  export default {
    name: "ModalPage",
    data() {
      return {
        number: 1,
        price: 0,
        resultPrice: "0",
        more:0,
        cartPage: false,
        memberships: false,
      };
    },
  
    props: {
      상품: Object, // 부모 요소가 가진 데이터의 원형  => 데이터 이름: 데이터 타입
      click: Number,
    },
  
    methods: {
      plus() {
        if (this.number < 20) {
          this.number += 1;
        }else{
          alert('한 번에 20개 까지 구매 가능합니다.')
        }
      },
      minus() {
        if (this.number > 1) {
          this.number -= 1;
         
        }
      },
      favBox(){
        let favBox = document.querySelector('.favBoxOBJ1');
        favBox.classList.toggle('fillFav');
      }   
    }
  };
  </script>
  
  <style>
  </style>